.box {
  width: 600px;
  height: 600px;
  cursor: pointer;
  border: 10px solid #000;
  flex-wrap: wrap;
  display: flex;
}

.sub-box {
  width: 33.3333%;
  height: 33.3333%;
  cursor: pointer;
  box-sizing: border-box;
  border: 3px solid #000;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-size: 100px;
  display: flex;
}

.markerO {
  width: 80%;
  height: 80%;
  box-sizing: border-box;
  border: 20px solid red;
  border-radius: 50%;
}

.markerX {
  width: 100%;
  height: 100%;
  position: relative;
}

.stick1, .stick2 {
  height: 100%;
  width: 25px;
  background-color: #00f;
  border-radius: 30px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%)rotate(45deg);
}

.stick2 {
  transform: translate(-50%)rotate(-45deg);
}

.yellow {
  background-color: #ff0;
}

